export default "import React, {useState} from 'react';\r\nimport {Carousel} from 'react-bootstrap';\r\n\r\nfunction Controlled() {\r\n  const [index, setIndex] = useState(0);\r\n\r\n  const handleSelect = (selectedIndex) => {\r\n    setIndex(selectedIndex);\r\n  };\r\n\r\n  return (\r\n    <Carousel activeIndex={index} onSelect={handleSelect}>\r\n      <Carousel.Item>\r\n        <img\r\n          className='d-block w-100'\r\n          src='https://via.placeholder.com/800x400?text=First slide&bg=373940'\r\n          alt='First slide'\r\n        />\r\n        <Carousel.Caption>\r\n          <h3>First slide label</h3>\r\n          <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>\r\n        </Carousel.Caption>\r\n      </Carousel.Item>\r\n      <Carousel.Item>\r\n        <img\r\n          className='d-block w-100'\r\n          src='https://via.placeholder.com/800x400?text=Second slide&bg=282c34'\r\n          alt='Second slide'\r\n        />\r\n\r\n        <Carousel.Caption>\r\n          <h3>Second slide label</h3>\r\n          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>\r\n        </Carousel.Caption>\r\n      </Carousel.Item>\r\n      <Carousel.Item>\r\n        <img\r\n          className='d-block w-100'\r\n          src='https://via.placeholder.com/800x400?text=Third slide&bg=20232a'\r\n          alt='Third slide'\r\n        />\r\n\r\n        <Carousel.Caption>\r\n          <h3>Third slide label</h3>\r\n          <p>\r\n            Praesent commodo cursus magna, vel scelerisque nisl consectetur.\r\n          </p>\r\n        </Carousel.Caption>\r\n      </Carousel.Item>\r\n    </Carousel>\r\n  );\r\n}\r\n\r\nexport default Controlled;\r\n";